







































































































































































































import { Vue, Component, Watch } from 'vue-property-decorator';

import { EStepShape } from '@/../enums';

import { ServiceDatetime, ServiceRegex, ServiceRole, ServiceTypeof, ServiceDialog, ServiceToast, ServiceServerConfig } from '@/../helpers';
import { ServerApiPath, WebPagePath } from '@/config';

import Card from '@/../components/card/card.vue';
import Step from '@/../components/step/step.vue';

// form
interface IFormItem {
    isShowCard: boolean;
}

interface IFormData {
    account: string;
    email: string;
    verification: string;
    password: string;
    confirmPassword: string;
}

@Component({
    components: {
        Step,
        Card,
    },
})
export default class VuePageClass extends Vue {
    private multipleStepItem = {
        templateKey: 'forget',
        labels: [this.$i18n.ForgetPassword_Step1, this.$i18n.ForgetPassword_Step2, this.$i18n.ForgetPassword_Step3, this.$i18n.ForgetPassword_Step4],
        // shape: EStepShape.circle,
        // barColor: '#537895',
    };

    private stepCotrolItem = {
        currentStep: 0,
        showCancel: true,
        showPrev: false,
        showNext: true,
        showReturnLogin: false,
        disablePrev: true,
        disableNext: true,
        disableSendVerifyCode: true,
    };

    private formDataOriginal: IFormData = {
        account: '',
        email: '',
        verification: '',
        password: '',
        confirmPassword: '',
    };
    private formData: IFormData = { ...this.formDataOriginal };

    private formDataState = {
        account: null,
        email: null,
        verification: null,
        password: null,
        confirmPassword: null,
    };

    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    //////////////////// Init Data ////////////////////

    //////////////////// Init Select Option  ////////////////////

    //////////////////// Form Show ////////////////////

    private checkButton() {
        switch (this.stepCotrolItem.currentStep) {
            case 0:
                this.stepCotrolItem.showCancel = true;
                this.stepCotrolItem.showPrev = false;
                this.stepCotrolItem.showNext = true;
                this.stepCotrolItem.showReturnLogin = false;

                this.stepCotrolItem.disablePrev = true;
                this.stepCotrolItem.disableNext = true;
                this.stepCotrolItem.disableSendVerifyCode = true;

                if (this.formData.account != '' && ServiceRegex.isEmail(this.formData.email)) {
                    this.stepCotrolItem.disableSendVerifyCode = false;
                    this.stepCotrolItem.disableNext = false;
                }
                break;
            case 1:
                this.stepCotrolItem.showCancel = true;
                this.stepCotrolItem.showPrev = true;
                this.stepCotrolItem.showNext = true;
                this.stepCotrolItem.showReturnLogin = false;

                this.stepCotrolItem.disablePrev = false;
                this.stepCotrolItem.disableNext = true;

                if (this.formData.verification != '') {
                    this.stepCotrolItem.disableNext = false;
                }
                break;
            case 2:
                this.stepCotrolItem.showCancel = true;
                this.stepCotrolItem.showPrev = true;
                this.stepCotrolItem.showNext = true;
                this.stepCotrolItem.showReturnLogin = false;

                this.stepCotrolItem.disablePrev = false;
                this.stepCotrolItem.disableNext = true;

                if (
                    ServiceRegex.isStrongPassword(this.formData.password) &&
                    this.formData.confirmPassword != '' &&
                    this.formData.password == this.formData.confirmPassword
                ) {
                    this.stepCotrolItem.disableNext = false;
                }
                break;
            case 3:
                this.stepCotrolItem.showCancel = false;
                this.stepCotrolItem.showPrev = false;
                this.stepCotrolItem.showNext = false;
                this.stepCotrolItem.showReturnLogin = true;

                this.stepCotrolItem.disablePrev = true;
                this.stepCotrolItem.disableNext = true;
        }
    }

    private clickNextStep() {
        switch (this.stepCotrolItem.currentStep) {
            case 0:
                this.submitStep1();
                break;
            case 1:
                this.submitStep2();
                break;
            case 2:
                this.submitStep3();
                break;
        }
    }

    private prevStep() {
        let stepRef: any = this.$refs.step;
        if (stepRef) {
            stepRef.prevStep();
            this.stepCotrolItem.currentStep--;
            this.checkButton();
        }
    }

    private nextStep() {
        let stepRef: any = this.$refs.step;
        if (stepRef) {
            stepRef.nextStep();
            this.stepCotrolItem.currentStep++;
            this.checkButton();
        }
    }

    private pageToLogin() {
        this.$router.push(WebPagePath.Login);
    }

    private stepComplete() {}

    //////////////////// Form Data ////////////////////

    private formDataStatus(key: string) {
        return this.formDataState[key];
    }

    private formDataChange(event: any, key: string) {
        switch (key) {
            case 'account':
                this.formDataState.account = this.formData.account != '';
                break;
            case 'email':
                this.formDataState.email = ServiceRegex.isEmail(this.formData.email);
                break;
            case 'verification':
                this.formDataState.verification = this.formData.verification != '';
                break;
            case 'password':
                this.formDataState.password = ServiceRegex.isStrongPassword(this.formData.password);
                break;
            case 'confirmPassword':
                this.formDataState.confirmPassword = this.formData.password == this.formData.confirmPassword;
                break;
        }

        if (this.formData.account != '' && ServiceRegex.isEmail(this.formData.email)) {
            this.stepCotrolItem.disableSendVerifyCode = false;
        }
        this.checkButton();
    }

    private formDataClear() {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
    }

    //////////////////// API ////////////////////

    private async submitStep1() {
        let url = ServiceServerConfig.url(ServerApiPath.ForgetPasswordStep1_C);
        let param = {
            username: this.formData.account,
            email: this.formData.email,
        };

        ServiceToast.success(this.$i18n.ForgetPassword_Step1Success);
        this.nextStep();
    }

    private async submitStep2() {
        let url = ServiceServerConfig.url(ServerApiPath.ForgetPasswordStep2_C);
        let param = {
            username: this.formData.account,
            verification: this.formData.verification,
        };

        ServiceToast.success(this.$i18n.ForgetPassword_Step2Success);
        this.nextStep();
    }

    private async submitStep3() {
        let url = ServiceServerConfig.url(ServerApiPath.ForgetPasswordStep3_C);
        let param = {
            username: this.formData.account,
            password: this.formData.password,
            verification: this.formData.verification,
        };
        this.nextStep();
    }
}
